import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/approval/request";

class ApprovalRequestService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllForApprovalRequestSummary= (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  
  getByUid = (uid: string) => {
    const url = `${DEFAULT_PATH}/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  approveRequest = (uid: string, obj: any) => {
    const url = `${DEFAULT_PATH}/approve/${uid}`;
    return this.makePostRequestWithAuth(url, obj);
  };
  rejectRequest = (uid: string, obj: any) => {
    const url = `${DEFAULT_PATH}/reject/${uid}`;
    return this.makePostRequestWithAuth(url, obj);
  };
}

export default new ApprovalRequestService();
